.main-search-results,
.user-detail-main {
  font-size: 1.6rem;
  color: $black-text-color;
  display: flex;
  flex: 1;

  &__repository-results {
    margin: 3rem 0rem;
    padding: 0rem 7rem;
    width: 100%;
    overflow: auto;
    height: 90vh;
    @media screen and (max-width: 600px) {
      padding: 0 2rem;
    }

    &__title {
      margin-bottom: 3rem;
    }

    &__result {
      padding: 2rem 0;
      border-bottom: 1px solid $line-color;
      margin: 1rem 0;

      &:hover {
        background-color: $ligtest-blue;
        border-radius: 10px;
      }

      &__title {
        display: flex;
        align-items: center;
      }

      & > * {
        margin-bottom: 1rem;
      }
    }
  }
}

.user-picture {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  margin-right: 1rem;
}
